<template>
  <mdb-container>
    <mdb-row>
      <mdb-col
        style="margin-bottom: 1em;"
        v-for="(project, i) in projects"
        :key="i"
        :md="project.col"
      >

          <mdb-card
            v-viewer="{
          button: false,
          navbar: false,
          title: false,
          toolbar: false,
          tooltip: false,
          zoomable: false,
          rotatable: false,
          scalable: false,
          keyboard: false
        }"
            class="images"
            style="background-color: rgba(4, 24, 39, 0.85);"
          >
            <!-- <mdb-card-image
              v-if="project.img.png"
              class="project"
              :src="require(`@/assets/${project.img.webp}`)"
              :alt="project.description"
            ></mdb-card-image>-->
            <picture
              v-if="project.img.png"
              style="margin: 0.5em auto;"
              class="img-fluid W-100"
            >
              <source class="lazyload"  media="(max-width: 650px)" :srcset="require(`@/assets/${project.img.thumb}`)" type="image/png">
              <img
                class="img-fluid lazyload"
                style=" height: 30vh"
                :src="require(`@/assets/${project.img.placeholder}`)"
                :data-src="require(`@/assets/${project.img.webp}`)"
              >
            </picture>
            <img
              v-else
              style="height: 20vh; margin: 1em;"
              :src="require(`@/assets/${project.img.local}`)"
              alt
            >
            <mdb-card-body style="background: #000000b3; margin-top: 1em;">
              <mdb-card-title
                style="font-size: 1.9rem; color:  rgb(38, 171, 209);"
              >{{project.title}}</mdb-card-title>
              <mdb-card-text
                style="font-size: 1.2rem; color:#a2b3c1 !important;"
              >{{project.description}}</mdb-card-text>
              <a v-if="project.projectURL" :href="project.projectURL" target="_blank">
                <mdb-btn
                  style="background: rgb(7, 26, 41) !important;"
                  color="primary"
                >{{`Visit ${project.title}`}}</mdb-btn>
              </a>
            </mdb-card-body>
          </mdb-card>
   
      </mdb-col>
    </mdb-row>
  </mdb-container>
</template>


<script>
import {
  mdbContainer,
  mdbRow,
  mdbCol,
  mdbCard,
  mdbCardImage,
  mdbCardBody,
  mdbCardTitle,
  mdbCardText,
  mdbBtn,
  mdbModal,
  mdbModalHeader,
  mdbModalTitle,
  mdbModalBody,
  mdbModalFooter
} from "mdbvue";
export default {
  name: "ProjectsContainer",
  props: ["projects"],
  components: {
    mdbContainer,
    mdbRow,
    mdbCol,
    mdbCard,
    mdbCardImage,
    mdbCardBody,
    mdbCardTitle,
    mdbCardText,
    mdbBtn,
    mdbModal,
    mdbModalHeader,
    mdbModalTitle,
    mdbModalBody,
    mdbModalFooter
  },
  data() {
    return {
      projectModal: {
        project: {},
        visible: false
      }
    };
  },
  methods: {
    goToProject(projectURL) {
      this.$router.push(projectURL);
    }
  }
};
</script>

<style>
.project img {
  height: 30vh;
  margin-top: 1em;
}
.card .card-body .card-text {
  color: #a2b3c1 !important;
  font-size: 1rem;
  font-weight: 400;
}
.btn-primary {
  background-color: #071a29 !important;
  color: #fff;
}
.modal-body {
  background: #041019;
}
</style>
